<template>
	<view class="content">
		<!-- Ë¥≠Áâ©ËΩ¶Ê†áÈ¢ò -->
		<view class="header">
			<text class="title">Ë¥≠Áâ©ËΩ¶</text>
			<text class="edit-btn" @click="toggleEditMode">{{isEditMode ? 'ÂÆåÊàê' : 'ÁºñËæë'}}</text>
		</view>
		
		<!-- Ë¥≠Áâ©ËΩ¶ÂàóË°® -->
		<view class="cart-list" v-if="cartItems.length > 0">
			<view class="cart-item" v-for="(item, index) in cartItems" :key="index">
				<!-- ÈÄâÊã©Ê°Ü -->
				<view class="checkbox" @click="toggleSelect(index)">
					<text class="checkbox-icon" :class="{ selected: item.selected }">
						{{item.selected ? '‚úì' : ''}}
					</text>
				</view>
				
				<!-- ÂïÜÂìÅ‰ø°ÊÅØ -->
				<image :src="item.image" class="item-image" mode="aspectFill"></image>
				<view class="item-info">
					<text class="item-name">{{item.name}}</text>
					<text class="item-spec" v-if="item.specName">ËßÑÊ†ºÔºö{{item.specName}}</text>
					<view class="item-price-section">
						<text class="item-price">¬•{{item.price}}</text>
						<text class="item-original-price" v-if="item.originalPrice">¬•{{item.originalPrice}}</text>
					</view>
				</view>
				
				<!-- Êï∞ÈáèÊéßÂà∂ -->
				<view class="quantity-control">
					<button class="quantity-btn" @click="decreaseQuantity(index)" :disabled="item.quantity <= 1">-</button>
					<text class="quantity-text">{{item.quantity}}</text>
					<button class="quantity-btn" @click="increaseQuantity(index)" :disabled="item.quantity >= item.stock">+</button>
				</view>
				
				<!-- Âà†Èô§ÊåâÈíÆÔºàÁºñËæëÊ®°ÂºèÔºâ -->
				<view class="delete-btn" v-if="isEditMode" @click="removeItem(index)">
					<text class="delete-icon">üóëÔ∏è</text>
				</view>
			</view>
		</view>
		
		<!-- Á©∫Ë¥≠Áâ©ËΩ¶ -->
		<view class="empty-cart" v-else>
			<text class="empty-icon">üõí</text>
			<text class="empty-text">Ë¥≠Áâ©ËΩ¶ÊòØÁ©∫ÁöÑ</text>
			<button class="go-shopping-btn" @click="goShopping">ÂéªË¥≠Áâ©</button>
		</view>
		
		<!-- Â∫ïÈÉ®ÁªìÁÆóÊ†è -->
		<view class="bottom-bar" v-if="cartItems.length > 0">
			<view class="select-all">
				<view class="checkbox" @click="toggleSelectAll">
					<text class="checkbox-icon" :class="{ selected: isAllSelected }">
						{{isAllSelected ? '‚úì' : ''}}
					</text>
				</view>
				<text class="select-all-text">ÂÖ®ÈÄâ</text>
			</view>
			
			<view class="total-section">
				<view class="total-info">
					<text class="total-label">ÂêàËÆ°Ôºö</text>
					<text class="total-price">¬•{{totalPrice}}</text>
				</view>
				<text class="total-desc">Â∑≤ÈÄâ{{selectedCount}}‰ª∂ÂïÜÂìÅ</text>
			</view>
			
			<button class="checkout-btn" @click="checkout" :disabled="selectedCount === 0">
				ÁªìÁÆó({{selectedCount}})
			</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				cartItems: [],
				isEditMode: false
			}
		},
		computed: {
			// ÊòØÂê¶ÂÖ®ÈÄâ
			isAllSelected() {
				return this.cartItems.length > 0 && this.cartItems.every(item => item.selected);
			},
			// ÈÄâ‰∏≠ÂïÜÂìÅÊï∞Èáè
			selectedCount() {
				return this.cartItems.filter(item => item.selected).length;
			},
			// ÊÄª‰ª∑
			totalPrice() {
				return this.cartItems
					.filter(item => item.selected)
					.reduce((total, item) => total + (item.price * item.quantity), 0)
					.toFixed(2);
			}
		},
		onShow() {
			this.loadCartItems();
		},
		methods: {
			// Âä†ËΩΩË¥≠Áâ©ËΩ¶Êï∞ÊçÆ
			loadCartItems() {
				const cart = uni.getStorageSync('cart') || [];
				this.cartItems = cart.map(item => ({
					...item,
					selected: true // ÈªòËÆ§ÈÄâ‰∏≠
				}));
			},
			
			// ÂàáÊç¢ÁºñËæëÊ®°Âºè
			toggleEditMode() {
				this.isEditMode = !this.isEditMode;
			},
			
			// ÂàáÊç¢ÂïÜÂìÅÈÄâÊã©
			toggleSelect(index) {
				this.cartItems[index].selected = !this.cartItems[index].selected;
				this.updateCart();
			},
			
			// ÂÖ®ÈÄâ/ÂèñÊ∂àÂÖ®ÈÄâ
			toggleSelectAll() {
				const newState = !this.isAllSelected;
				this.cartItems.forEach(item => {
					item.selected = newState;
				});
				this.updateCart();
			},
			
			// ÂáèÂ∞ëÊï∞Èáè
			decreaseQuantity(index) {
				if (this.cartItems[index].quantity > 1) {
					this.cartItems[index].quantity--;
					this.updateCart();
				}
			},
			
			// Â¢ûÂä†Êï∞Èáè
			increaseQuantity(index) {
				if (this.cartItems[index].quantity < this.cartItems[index].stock) {
					this.cartItems[index].quantity++;
					this.updateCart();
				}
			},
			
			// Âà†Èô§ÂïÜÂìÅ
			removeItem(index) {
				uni.showModal({
					title: 'Á°ÆËÆ§Âà†Èô§',
					content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ÂïÜÂìÅÂêóÔºü',
					success: (res) => {
						if (res.confirm) {
							this.cartItems.splice(index, 1);
							this.updateCart();
							uni.showToast({
								title: 'Âà†Èô§ÊàêÂäü',
								icon: 'success'
							});
						}
					}
				});
			},
			
			// Êõ¥Êñ∞Ë¥≠Áâ©ËΩ¶Â≠òÂÇ®
			updateCart() {
				uni.setStorageSync('cart', this.cartItems);
			},
			
			// ÂéªË¥≠Áâ©
			goShopping() {
				uni.navigateBack();
			},
			
			// ÁªìÁÆó
			checkout() {
				const selectedItems = this.cartItems.filter(item => item.selected);
				if (selectedItems.length === 0) {
					uni.showToast({
						title: 'ËØ∑ÈÄâÊã©Ë¶ÅÁªìÁÆóÁöÑÂïÜÂìÅ',
						icon: 'none'
					});
					return;
				}
				
				uni.navigateTo({
					url: `/pages/shop/checkout?items=${encodeURIComponent(JSON.stringify(selectedItems))}&type=cart`
				});
			}
		}
	}
</script>

<style lang="scss">
	.content {
		background-color: #fef8fa; // Êõ¥Ê∑°ÁöÑÊµÖÁ≤âËâ≤ËÉåÊôØ
		min-height: 100vh;
		padding-bottom: 120rpx;
	}
	
	/* Â§¥ÈÉ® */
	.header {
		background-color: #fff;
		padding: 30rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1rpx solid #f0f0f0;
		
		.title {
			font-size: 36rpx;
			font-weight: bold;
			color: #333;
		}
		
		.edit-btn {
			font-size: 28rpx;
			color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
		}
	}
	
	/* Ë¥≠Áâ©ËΩ¶ÂàóË°® */
	.cart-list {
		.cart-item {
			background-color: #fff;
			margin: 20rpx;
			padding: 30rpx;
			border-radius: 16rpx;
			display: flex;
			align-items: center;
			gap: 20rpx;
			
			.checkbox {
				width: 40rpx;
				height: 40rpx;
				border: 2rpx solid #ddd;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				
				.checkbox-icon {
					font-size: 24rpx;
					color: transparent;
					
					&.selected {
						color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					}
				}
			}
			
			.item-image {
				width: 120rpx;
				height: 120rpx;
				border-radius: 12rpx;
			}
			
			.item-info {
				flex: 1;
				
				.item-name {
					font-size: 28rpx;
					font-weight: bold;
					color: #333;
					display: block;
					margin-bottom: 8rpx;
				}
				
				.item-spec {
					font-size: 24rpx;
					color: #666;
					display: block;
					margin-bottom: 8rpx;
				}
				
				.item-price-section {
					display: flex;
					align-items: center;
					gap: 10rpx;
					
					.item-price {
						font-size: 28rpx;
						font-weight: bold;
						color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					}
					
					.item-original-price {
						font-size: 24rpx;
						color: #999;
						text-decoration: line-through;
					}
				}
			}
			
			.quantity-control {
				display: flex;
				align-items: center;
				gap: 20rpx;
				
				.quantity-btn {
					width: 50rpx;
					height: 50rpx;
					background-color: #f0f0f0;
					border-radius: 50%;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 28rpx;
					color: #333;
					
					&:disabled {
						background-color: #f5f5f5;
						color: #ccc;
					}
				}
				
				.quantity-text {
					font-size: 28rpx;
					font-weight: bold;
					color: #333;
					min-width: 60rpx;
					text-align: center;
				}
			}
			
			.delete-btn {
				width: 60rpx;
				height: 60rpx;
				background-color: #ff4757;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				
				.delete-icon {
					font-size: 28rpx;
				}
			}
		}
	}
	
	/* Á©∫Ë¥≠Áâ©ËΩ¶ */
	.empty-cart {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 200rpx 0;
		
		.empty-icon {
			font-size: 120rpx;
			margin-bottom: 30rpx;
		}
		
		.empty-text {
			font-size: 32rpx;
			color: #999;
			margin-bottom: 40rpx;
		}
		
		.go-shopping-btn {
			background-color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
			color: #fff;
			padding: 20rpx 60rpx;
			border-radius: 40rpx;
			font-size: 28rpx;
		}
	}
	
	/* Â∫ïÈÉ®ÁªìÁÆóÊ†è */
	.bottom-bar {
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #fff;
		padding: 20rpx 30rpx;
		border-top: 1rpx solid #f0f0f0;
		display: flex;
		align-items: center;
		gap: 20rpx;
		
		.select-all {
			display: flex;
			align-items: center;
			gap: 10rpx;
			
			.checkbox {
				width: 40rpx;
				height: 40rpx;
				border: 2rpx solid #ddd;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				
				.checkbox-icon {
					font-size: 24rpx;
					color: transparent;
					
					&.selected {
						color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					}
				}
			}
			
			.select-all-text {
				font-size: 28rpx;
				color: #333;
			}
		}
		
		.total-section {
			flex: 1;
			
			.total-info {
				display: flex;
				align-items: center;
				gap: 10rpx;
				margin-bottom: 4rpx;
				
				.total-label {
					font-size: 28rpx;
					color: #333;
				}
				
				.total-price {
					font-size: 32rpx;
					font-weight: bold;
					color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
				}
			}
			
			.total-desc {
				font-size: 24rpx;
				color: #999;
			}
		}
		
		.checkout-btn {
			background-color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
			color: #fff;
			padding: 20rpx 40rpx;
			border-radius: 40rpx;
			font-size: 28rpx;
			font-weight: bold;
			
			&:disabled {
				background-color: #ccc;
			}
		}
	}
</style> 